.App {
  text-align: center;
  color: white;
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
}

.App-header {
  height: 50px;
  padding: 10px;
  margin-bottom: 10px;
  border-bottom: 3px solid white;
  display: -webkit-flex;
  display: flex;
}

.App-menu {
  margin: auto 0;
}

.App-title {
  font-size: 1.5em;
  margin: auto;
}

.App-body {
  -webkit-flex: 2 1;
          flex: 2 1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-size: 20px;
}
.route-container {
  height: 100%;
  width: 100%;
}
.App-cities {
  -webkit-flex: 2 1;
          flex: 2 1;
}

@media(min-width: 768px) {
  .App-cities {
    width: 50%;
    margin: auto;
  }
}


.scrollbar::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 8px;
	background-color: #F5F5F5;
}

.scrollbar::-webkit-scrollbar
{
	height: 8px;
	background-color: #5bd3ff;
}

.scrollbar::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: #555;
}


.header {
  grid-column-start: 2;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 1;
}

.add-city {
  padding: 10px;
  border-top: 1px solid white;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-direction: row;
          flex-direction: row;
  display: -webkit-flex;
  display: flex;
}

.add-city-input {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.add-city-input-container {
  background-color: white;
  border-radius: 50px;
  padding: 10px;
}

.add-city-input-field__input {
  border: none;
  outline: none;
  width: 80%;
  font-size: 16px;
}

.add-city__header {
  white-space: nowrap;
  margin-right: 10px;
  padding-top: 10px;
}
.city-handler {
  background-color: var(--sun);
}
.error {
  background-color: lightcoral;
  border: 1px solid red;
  padding: 10px;
}

.error-text {
  padding-left: 10px;
}

.detailed-data__label {
  font-size: 16px;
}

.selected-weather-view {
  height: 100%;
  display: grid;
  grid-template-rows: [temperatur]90% [details]auto;
}

.detailed-data {
  display: grid;
  grid-template-columns: [wind]33% [humidity]33% [pressure]33%;
}


.current-weather {
  font-size: 40vh;
}


.weather-displayer {
  display: grid;
  grid-template-rows: 15% 70% 15%;
  grid-template-columns: 5% 90% 5%;
  height: 100%;
  width: 100%;
  background-size: cover;
  background-repeat:   no-repeat;
  background-position: center center;
}

.footer__day {
  text-align: left;
  padding-top: 10px;
}

.footer {
  grid-column-start: 2;
  grid-column-end: 2;
  grid-row: 3;
}

.weather-list {
  overflow-y: auto;
  display: -webkit-flex;
  display: flex;
  border-top: 2px solid white;
}

.city-item {
  grid-row-start: 2;
  grid-row-end: 2;
  grid-column-start: 2;
  grid-column-end: 2;
}

body::after{
  /* preload background images*/
  position:absolute; width:0; height:0; overflow:hidden; z-index:-1;
  content:url(/weather-app/static/media/sunny.22398947.jpg) url(/weather-app/static/media/rain.f7b1bb6d.jpg) url(/weather-app/static/media/snow.32a8591d.jpg) url(/weather-app/static/media/clouds.0679f82d.jpg) url(/weather-app/static/media/night.5256e3d1.jpg);
}

.weather-displayer--sun {
  background-image: url(/weather-app/static/media/sunny.22398947.jpg);
}
.weather-displayer--rain {
  background-image: url(/weather-app/static/media/rain.f7b1bb6d.jpg);
}
.weather-displayer--snow {
  background-image: url(/weather-app/static/media/snow.32a8591d.jpg);
}
.weather-displayer--clouds {
  background-image: url(/weather-app/static/media/clouds.0679f82d.jpg);
}
.weather-displayer--night {
  background-image: url(/weather-app/static/media/night.5256e3d1.jpg);
}
.day-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  border-left: 1px solid white;
}

.day-weather-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.day-title {
  text-align: left;
  white-space: nowrap;
  padding: 0 10px;
}

.weather-list-item {
  text-decoration: none;
  padding-left: 12px;
  color: white;
}

.weather-list-item--selected {
  opacity: 0.3;
}
html, body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  
  --sun: #5bd3ff;
  --clouds: #b3b3b3;
  --rain: #575757;
  --snow: #7e7e7e;
  --night: rgb(4, 11, 49);
}

button {
  border: 0;
  outline: none !important;
  background-color: inherit;
  -webkit-align-items: center;
          align-items: center;
  cursor: pointer;
}

a {
  color: inherit;
  text-decoration: none;
}

.full-scale {
  width: 100%;
  height: 100%;
}
